.NotesListItem {
    border-bottom: 1px solid #f2f2f2;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    display: -ms-flexbox;
    display: flex;
}

.NotesListItem:last-child {
    border-bottom: none;
}

.NotesListItem > .avatar {
    background-color: #ff2f5a;
    border-radius: 5px;
    margin: 10px 10px 10px 0;
    width: 50px;
    height: 50px;
}

.NotesListItem > .message {
    margin: 10px 0 10px 10px;
    -ms-flex: 1 1;
        flex: 1 1;
    word-break: break-all;
}



