.Panel {
    background-color: #ffffff;
    -webkit-box-shadow: 0 0 5px #727376;
            box-shadow: 0 0 5px #727376;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex: 1 1 40%;
        flex: 1 1 40%;
    padding: 20px;
    margin: 10px;
}

.Panel > .title {
    border-bottom: 1px solid #f2f2f2;
    font-size: 25px;
    color: #616573;
    margin-bottom: 10px;
}

.Panel > .body {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex: 1 1;
        flex: 1 1;
    height: 0;
}

@media (max-width: 1000px) {
    .Panel {
        -ms-flex: 1 1 100%;
            flex: 1 1 100%;
    }

    .Panel > .body {
        max-height: 50vh;
    }
}

@media (max-width: 500px) {
    .Panel > .body {
        max-height: 100vh;
        height: auto;
    }
}



